.attack-index {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(../../assets/imgs/attack/bg.png) no-repeat;
  background-size: 100% 100%;
}
.attack-index .bottom {
  width: 908px;
  height: 270px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  margin-left: -454px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
  z-index: 99;
}
.attack-index .bottom .title {
  height: 50px;
  line-height: 50px;
  color: #fff;
  font-size: 20px;
  text-indent: 50px;
}
.attack-index .bottom .tab {
  margin-top: 10px;
  border-bottom: 1px solid #053A93;
  padding-bottom: 10px;
}
.attack-index .bottom .tab li {
  display: inline-block;
  width: 100px;
  height: 35px;
  line-height: 35px;
  color: #fff;
  font-size: 15px;
  margin: 0 40px;
  text-align: center;
}
.attack-index .bottom .content {
  height: 150px;
  overflow: hidden;
}
.attack-index .bottom .ul {
  margin-top: 10px;
}
.attack-index .bottom .row li {
  display: inline-block;
  width: 70px;
  height: 35px;
  line-height: 35px;
  color: #BFBFBF;
  font-size: 15px;
  margin: 0 55px;
  text-align: center;
  text-overflow: ellipsis;
  /* 文本溢出显示省略标记 是一种生声明需要配合下面俩个属性才能生效*/
  white-space: nowrap;
  /*强制文本一行显示*/
  overflow: hidden;
  /*溢出隐藏*/
}
.attack-index .bottom .row li.di {
  color: #008CEF;
}
.attack-index .bottom .row li.hengao {
  color: #FF0042;
}
.attack-index .bottom .row li.gao {
  color: #FB8E01;
}
.attack-index .bottom .row li.zhong {
  color: #CCA30A;
}
.attack-index .bottom .row li.hendi {
  color: #00C16A;
}
