.attack-index .left-top ul li {
  display: inline-block;
  width: 90px;
  text-align: center;
  color: #B0BBE3;
  font-size: 15px;
}
.attack-index .left-top ul li .num {
  font-weight: bold;
}
.attack-index .left-top ul li:nth-child(1) .num {
  color: #8492E5;
}
.attack-index .left-top ul li:nth-child(2) .num {
