.attack-index .left-top .list {
  width: 366px;
  margin: 30px auto;
}
.attack-index .left-top .list .item {
  display: inline-block;
  color: #fff;
  text-align: center;
  margin: 0 20px;
}
.attack-index .left-top .list .item .bg {
