  height: 82px;
  background: url(../../assets/imgs/attack/yuan.png);
  line-height: 82px;
}
.attack-index .left-top .list .item p {
  font-size: 14px;
}
.attack-index > .title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 80px;
  text-align: center;
  color: #f6f6f8;
  background: url(../../assets/imgs/index/head.png) no-repeat;
  background-size: 100%;
  background-position: bottom center;
  z-index: 10;
}
.attack-index > .title .txt-center h1 {
  font-size: 35px;
  font-weight: 400;
}
.attack-index > .title .txt-center p {
  font-size: 14px;
  font-weight: 400;
}
