  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(../../assets/imgs/attack/bg.png) no-repeat;
  background-size: 100% 100%;


  .bottom{
    width: 908px;
    height: 270px;
    position: absolute;
    bottom: 30px;
    left: 50%;
    margin-left: -454px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    z-index: 99;
    .title{
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
      text-indent: 50px;
    }
    .tab{
      margin-top: 10px;
      border-bottom: 1px solid #053A93;
      padding-bottom: 10px;
      li{
        display: inline-block;
        width: 100px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 15px;
        margin: 0 40px;
        text-align: center;
      }
    }
    .content{
      height: 150px;
      overflow: hidden;
    }
    .ul{
      margin-top: 10px;
    }
    .row{
      li{
        display: inline-block;
        width: 70px;
        height: 35px;
        line-height: 35px;
        color: #BFBFBF;
        font-size: 15px;
        margin: 0 55px;
        text-align: center;
        text-overflow: ellipsis; /* 文本溢出显示省略标记 是一种生声明需要配合下面俩个属性才能生效*/
        white-space:nowrap; /*强制文本一行显示*/
        overflow: hidden; /*溢出隐藏*/
      }
      li.di{
        color: #008CEF;
      }
      li.hengao{
        color: #FF0042;
      }
      li.gao{
        color: #FB8E01;
      }
      li.zhong{
        color: #CCA30A;
      }
      li.hendi{
        color: #00C16A;
      }
    }
  }

  .right-bottom{
    width: 400px;
    height: 350px;
    position: absolute;
    right: 30px;
    bottom: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    z-index: 99;
    .title{
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
      text-indent: 50px;
    }
    .tab{
      padding-left: 30px;
      border-bottom: 1px solid #053A93;
        padding-bottom: 10px;
      margin-bottom: 10px;
      li{
        display: inline-block;
        width: 50px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 15px;
        margin: 0 30px;
        text-align: center;
      }
    }
    .content{
      height: 215px;
      overflow: hidden;
    }
    .row{
      margin-top: 10px;
      margin-left: 30px;
      li{
        display: inline-block;
        width: 70px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 15px;
        margin: 0 20px;
        text-align: center;
        img{
          position: relative;
          top: 4px;
          margin-right: 5px;
        }
      }
    }
  }


  .right-top{
    width: 400px;
    height: 350px;
    position: absolute;
    right: 30px;
    top: 80px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    z-index: 99;
    .title{
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
      text-indent: 50px;
    }
    .tab{
      padding-left: 30px;
      border-bottom: 1px solid #053A93;
      padding-bottom: 10px;
      margin-bottom: 10px;
      li{
        display: inline-block;
        width: 50px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 15px;
        margin: 0 30px;
        text-align: center;
      }
    }
    .content{
      height: 215px;
      overflow: hidden;
    }
    .row{
      margin-top: 10px;
      margin-left: 30px;
      li{
        display: inline-block;
        width: 70px;
        height: 35px;
        line-height: 35px;
        color: #fff;
        font-size: 15px;
        margin: 0 20px;
        text-align: center;
        img{
          position: relative;
          top: 4px;
          margin-right: 5px;
        }
      }
    }
  }

  .left-bottom{
    width: 400px;
    height: 400px;
    position: absolute;
    bottom: 30px;
    left: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    z-index: 99;
    .title{
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
      text-indent: 50px;
    }
  }


  .left-top{
    width: 400px;
    height: 350px;
    position: absolute;
    top: 80px;
    left: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    z-index: 99;
    .title{
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
      text-indent: 50px;
    }
    ul{
      width: 360px;
      margin: 0 auto;
      margin-top: 30px;
      li{
        display: inline-block;
        width: 90px;
        text-align: center;
        color: #B0BBE3;
        font-size: 15px;
        .num{
          font-weight: bold;
        }
      }
      li:nth-child(1) .num{
        color: #8492E5;
      }
      li:nth-child(2) .num{
        color: #01EAF9;
      }
      li:nth-child(3) .num{
        color: #548EC9;
      }
      li:nth-child(4) .num{
        color: #FE717B;
      }
    }
    .list{
      width: 366px;
      margin: 30px auto;
      .item{
        display: inline-block;
        color: #fff;
        text-align: center;
        margin: 0 20px;
        .bg{
          width: 82px;
          height: 82px;
          background: url(../../assets/imgs/attack/yuan.png);
          line-height: 82px;
        }
        p{
          font-size: 14px;
        }
      }
    }
  }

  >.title{
    position: absolute;
    top: 0;
    width: 100%;
    height: 80px;
    text-align: center;
    color: #f6f6f8;
    background: url(../../assets/imgs/index/head.png) no-repeat;
    background-size: 100%;
    background-position: bottom center;
    z-index: 10;
    .txt-center{
          h1{
            font-size: 35px;
            font-weight: 400;
          }
          p{
            font-size: 14px;
            font-weight: 400;
          }
        }
  }
}
