  position: relative;
  width: 100vw;
  height: 100vh;
  ul{
    width: 100vw;
    position: absolute;
    bottom: 320px;
    color: #fff;
    text-align: center;
    li{
      display: inline-block;
      height: 12px;
      line-height: 12px;
      font-size: 12px;
      margin-right: 20px;
      span{
        display: inline-block;
        width: 15px;
        height: 12px;
        background:#FC2900;
      }
    }
  }
}
.map{
  position: absolute;
  left: 50%;
  top: 25%;
  margin-top: -200px;
  margin-left: -400px;
}
